body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h2 {
    text-align: center;
    text-transform: uppercase;
    color: #285473;
    font-size: 24px;
    font-weight: 100;
}

.commonBtn {
    display: block;
    box-sizing: border-box;
    cursor: pointer;
    text-decoration: none;
    color: white;
    border: none;
    border-radius: 5px;
    background-color: #4a76a8;
    font-size: 16px;
    line-height: 24px;
}

.commonBtn:hover {
    background-color: #425884;
}

.redirectToLoginPage {
    display: block;
    text-align: center;
    color: #285473;
    font-size: 24px;
    font-weight: 100;
}

.redirectToLoginPage:hover {
    text-decoration: none;
}

.app-wrapper {
  display: grid;
  width: 1280px;
  margin: 0 auto;

  grid-gap: 20px;
  grid-template-areas: "header header" "sidebar main-content";
  grid-template-columns: 30% 70%;
  grid-template-rows: 80px 1fr;
}

.app-wrapper__content {
  grid-area: main-content;
}

.sidebar {
  grid-area: sidebar;
}
